.category {
    padding-top: 30px;
    display: flex;
    flex-direction: row;
    .category_content {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        .category_item {
            padding: 20px;
            width: 300px;
            border: 3px solid #989898;
            border-radius: 12px;
            cursor: pointer;
        }

        .cars {
            margin-bottom: 20px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            .cars_title {
                font-size: 20px;
                font-weight: bold;
            }
            .cars_subTitle {
                padding-top: 10px;
            }
            img {
                width: 100%;
                max-width: 260px;
            }
        }
    }
}

.category_item_active {
    border-color: #33ccff !important;
}
