.benefits {
    padding-top: 30px;
    padding-bottom: 90px;
    background-color: #fafafa;
}
.benefits_items {
    padding-top: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 20px;
    .benefits_item {
        padding-right: 10px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        background-color: #dcdcdc;
        border-radius: 15px;
        .benefits_item_svg {
            width: 140px;
            height: 140px;
        }
        .benefits_item_text {
            font-size: 24px;
            line-height: 1.3;
        }
    }
}
