.ourServices {
    .ourServices_content {
        .subTitle_text {
            width: 100%;
            max-width: 620px;
            margin: 0 auto;
            text-align: center;
        }
    }
}

.ourServicesSlider {
    padding: 50px 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.ourServices_item {
    padding: 20px;
    width: 100%;
    max-width: 350px;
    min-height: 380px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    border: 2px solid #33ccff;
    border-radius: 12px;
    .mainImg {
        display: flex;
        width: 100%;
        max-width: 306px;
        height: 100%;
        max-height: 204px;
    }
    .mainSvg {
        margin-top: 10px;
        width: 40px;
        height: 40px;
    }
    .item_title {
        margin: 10px 0;
        font-size: 16px;
        font-weight: bold;
    }
}

.swiper-button-next {
    right: 0;
}
.swiper-button-prev {
    left: 0;
}
