.mainSlider_slide {
    width: 100%;
    height: 570px;
}
.mainSlider_slide {
    display: flex;
    width: 100%;
    height: 570px;
    position: relative;
}
.mainSlide_img {
    display: flex;
    width: 100%;
    height: 520px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 9;
}
.mainSlider_slideContent{
    padding: 100px 0 0 120px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    z-index: 10;
    .slideContent_text{
        margin-bottom: 20px;
        padding: 10px 25px;
        font-size: 40px;
        font-weight: bold;
        color: #fff;
        background-color: #33ccff;
        border-radius: 5px;
    }
}