.aboutUs {
    margin-top: 50px;
    padding: 50px 0;
    background: url("../../assets/img/AboutUsBG.jpg");
    background-size: cover;
    position: relative;
    .aboutUsBG {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background: rgba(32, 30, 35, 0.498585);
        background: radial-gradient(
                442.92px at 23.75% 104.19%,
                rgba(158, 110, 69, 0.493291) 0%,
                rgba(28, 27, 33, 0.65) 100%
            ),
            radial-gradient(442.92px at 79.68% 19.86%, rgba(146, 96, 64, 0.78) 0%, rgba(36, 33, 41, 0.64) 100%);
    }
    .aboutUs_content {
        color: #fff;
        position: relative;
        .title {
            padding-bottom: 20px;
            width: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            font-size: 32px;
            font-weight: bold;
        }
        .aboutUs_content_text {
            padding-bottom: 20px;
        }
        .aboutUs_content_list {
            padding-bottom: 20px;
            padding-left: 30px;
        }
    }
}
